  }

  .nav-list-el {
    letter-spacing: .02em;
    color: var(--text-color);
    transition: var(--transition-dur-func);
    margin-top: 6px;
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.33;
    text-decoration: none;
    display: flex;
  }

  .nav-link:visited {
    color: var(--text-color);
  }

  .logo {
    margin-top: 20px;
    margin-left: 0;
    margin-right: 43px;
    padding: 0;
    line-height: 0;
  }

  .page-header {
    width: 100%;
  }

  .page-header__container {
    padding: 1px 16px;
  }

  .search-container {
    margin-top: 20px;
    margin-left: 176px;
    margin-right: 105px;
  }

  .toggleWrapper {
    align-items: center;
    gap: 70px;
    margin-top: 15px;
    display: flex;
    transform: translate3d(0%, 0%, 0);
  }

  .toggleWrapper input {
    position: absolute;
    left: -99em;
  }

  .toggle {
    cursor: pointer;
    width: 40px;
    height: 20px;
    background-color: FFFFFF;
    z-index: 2;
    border: 1px solid #fd5103;
    border-radius: 20px;
    transition: background-color .2s cubic-bezier(.445, .05, .55, .95);
    display: inline-block;
    position: absolute;
    top: 3px;
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 4px 7px 10px #69696926;
  }

  .toggle__handler {
    z-index: 1;
    width: 16px;
    height: 16px;
    background-color: #fd5103;
    border-radius: 50px;
    transition: all .4s cubic-bezier(.68, -.55, .265, 1.55);
    display: inline-block;
    position: relative;
    top: 1px;
    left: 2px;
    transform: rotate(-45deg);
    box-shadow: 0 2px 6px #0000004d;
  }

  input:checked + .toggle {
    background-color: #fd5103;
    border-color: #fff;
  }

  input:checked + .toggle:after {
    color: #fff;
  }

  input:checked + .toggle .toggle__handler {
    background-color: #fff;
    transform: translate3d(18px, 0, 0)rotate(0);
  }

  @keyframes blink {
    85% {
      transform: rotateX(0);
    }

    90% {
      transform: rotateX(90deg);
    }
  }
}

